@import '../../global.scss';

.contact {
    background-color: #fff;
    display: flex;

    @include mobile {
        flex-direction: column;

    }

    .left {
        flex: 1;
        overflow: hidden;
        img {
            width: 100%;
        }
    }

    .right {
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;       

        @include tablet {
            flex: 1.3;
        }

        h2 {
            font-size: 30px;

            @include mobile {
                    font-size: 25px;
            }

        } 
        form {
            width: 70%;
            height: 70%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-around;

            input {
                width: 300px;
                height: 30px;
                font-size: 14px;

                @include mobile {
                    width: 200px;
                    height: 20px;
                }

            }
            textarea {
                width: 300px;
                height: 200px;
                font-size: 14;

                @include mobile {
                 width: 200px;
                 height: 80px;   
                }

            }
            button {
                width: 150px;
                height: 30px;
                color: #fff;
                background-color: crimson;
                border: none;
                border-radius: 10px;
                font-weight: 500;
                cursor: pointer;
                &:focus {
                    outline: none;
                }
            }
            span {
                color: green;
            }
        }
    }

}